@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #264653;
}

.container {
  width: 100%;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 30px;
}

.container input {
  width: 200px;
  height: 30px;
  border: none;
  outline: none;
  padding: 20px;
  text-align: center;
  background-color: transparent;
  transition: all 0.3s linear;
  color: White;
  font-size: 1.2rem;
}

.input-container {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ff4842a7;
  border-radius: 10px;
}
span {
  color: #ff4842a7;
}
input::placeholder {
  color: white;
  transition: all 0.3s linear;
  color: #ff484240;
}
.container-city {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  gap: 10px;
  margin-top: 50px;
  font-size: 1.5rem;
  font-weight: bold;
  color: rgba(255, 166, 0, 0.761);
}

.search-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

button {
  padding: 10px;
  width: 50px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}

.invisible {
  display: none;
}

.button-container {
  display: flex;
  gap: 10px;
  padding: 10px;
}

.button-24 {
  background: #ff4742;
  border: 1px solid #ff4742;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-family: nunito, roboto, proxima-nova, "proxima nova", sans-serif;
  font-size: 16px;
  font-weight: 800;
  line-height: 16px;
  min-height: 40px;
  outline: 0;
  padding: 12px 14px;
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  width: 150px;
}

.button-24:hover,
.button-24:active {
  background-color: #ff4842a7;
  background-position: 0 0;
  color: white;
}

.button-24:active {
  transform: scale(0.98);
}
